// import Vue from 'vue'
import Vue from 'src/pages/base'
import { Component, Watch } from 'vue-property-decorator'
import template from './shouye.vue'

@Component({
    name: 'shouye',
    mixins: [template],
    components: {
    }
})
export default class Shouye extends Vue {
    cb_current = "1";
    pd_current = "1";
    zncbList = [
        {name:'默认页'},
        {name:'自动抄表'},
        {name:'自动结算'},
        {name:'自动收单'},
        {name:'自动预警'},
        {name:'自动催单'},
        {name:'自动停电'},
        {name:'自动复电'},
        {name:'智能运维'}
    ]
    pdjkList = [
        {name:'默认页'},
        {
            name:'配电拓扑',
            children:[
                {name:'配电拓扑'},
                {name:'配电室拓'},
                {name:'自动扑图展示抄表'},
            ]
        },
        {
            name:'自动巡检',
            children:[
                {name:'自动巡检'},
                {name:'定时巡检'}
            ]
        },
        {
            name:'智能报表',
            children:[
                {name:'智能报表'},
                {name:'配展示统计分析信息电室拓'}
            ]
        },
        {
            name:'预警告警'
        },
        {
            name:'恢复验证',
            children:[
                {name:'故障恢复自动验证'},
                {name:'上传照片手动验证'}
            ]
        }
    ]

    zncb = require("@/assets/zncb_1.jpg")
    pdjk =require("@/assets/pdjk_1.jpg")

    zncbSelect(index) {
        this.cb_current = index;  // 把当前点击元素的index，赋值给activeClass
        this.zncb = require("@/assets/zncb_"+index+".jpg")
    }
    pdjkSelect(index) {
        this.pd_current = index;
        this.pdjk =require( "@/assets/pdjk_"+index+".jpg")

    }
  

   
}




